/* eslint-disable */
// CSS
import "../sass/lbw.sass";
// JS
require('aframe-gltf-part-component');
require('aframe-orbit-controls');
import { RGBELoader } from "three/examples/jsm/loaders/RGBELoader";
import { EffectComposer } from "three/examples/jsm/postprocessing/EffectComposer";
import { RenderPass } from "three/examples/jsm/postprocessing/RenderPass";
import { UnrealBloomPass } from "three/examples/jsm/postprocessing/UnrealBloomPass";
import { OutputPass } from "three/examples/jsm/postprocessing/OutputPass";
import { OutlinePass } from "three/examples/jsm/postprocessing/OutlinePass";
import { GTAOPass } from "three/examples/jsm/postprocessing/GTAOPass";
import { ShaderPass } from "three/examples/jsm/postprocessing/ShaderPass.js";
import { FXAAShader } from "three/examples/jsm/shaders/FXAAShader.js";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader.js";
import { NoBlending } from "three";
import { gsap } from "gsap";
import { CustomOutlinePass } from "./CustomOutlinePass";
import FindSurfaces from "./FindSurfaces";
//
document.addEventListener('DOMContentLoaded', function() {
    const sceneEl = document.querySelector('#scene');
    sceneEl.setAttribute('load-complete', '');
});

//3D
AFRAME.registerComponent('load-complete', {
    init: function() {
        // SCENE
        const sceneEl = document.querySelector('#scene');
        const cameraEl = document.querySelector('#basic_camera');
        const lbwEl = document.querySelector('#lbw');

        new RGBELoader().load("images/bg1.hdr", function(texture){
            texture.mapping = THREE.EquirectangularReflectionMapping;
            texture.image.data = texture.image.data.map(value => value * 0.66);//0.93
                    sceneEl.object3D.environment = texture;
        });
        
        // cameraEl.getObject3D('camera')

        // 
        // const timeline = gsap.timeline();

        //  FOV 
        gsap.to(cameraEl.getObject3D('camera'), {
            delay: 0,//3
            fov: 60,
            ease: "power1.inOut",
            duration: 6,
            onUpdate: () => {
                cameraEl.getObject3D('camera').updateProjectionMatrix();
                cameraEl.setAttribute('orbit-controls', {
                    target: '0 0 0',
                    minDistance: 150 * 2 / cameraEl.getObject3D('camera').fov,
                    maxDistance: 150 * 2 / cameraEl.getObject3D('camera').fov,
                    initialPosition: '0 0 ' + 150 * 2 / cameraEl.getObject3D('camera').fov,
                    rotateSpeed: 2,
                    zoomSpeed: 2,
                    enablePan: false,
                    maxPolarAngle: 90
                });
            },
            onComplete: () => {
                // 
                cameraEl.setAttribute('orbit-controls', { enableRotate: true });
            }
        });

        // Bloom
        const renderScene = new RenderPass( sceneEl.object3D, sceneEl.camera );
        const bloomPass = new UnrealBloomPass( new THREE.Vector2( window.innerWidth, window.innerHeight ), 1.5, 0.4, 0.85 );
        bloomPass.threshold = 1;
        bloomPass.strength = 0.33;
        bloomPass.radius = 0;

        const outputPass = new OutputPass();
        const renderer = sceneEl.renderer;
        renderer.setSize(window.innerWidth, window.innerHeight);
        const render = renderer.render;

        // Outline pass
        const customOutline = new CustomOutlinePass(
            new THREE.Vector2(window.innerWidth, window.innerHeight),
            sceneEl.object3D,
            sceneEl.camera
        );
        customOutline.FXAA = true; //  FXAA 
        customOutline.outlineColor = new THREE.Color(0x000000); // 
        customOutline.depthBias = 0.9;
        customOutline.depthMult = 20;
        customOutline.normalBias = 1;
        customOutline.normalMult = 1;
        customOutline.cameraNear = 0.1;
        customOutline.cameraFar = 100;

        const effectFXAA = new ShaderPass(FXAAShader);
        effectFXAA.uniforms['resolution'].value.set(1 / window.innerWidth, 1 / window.innerHeight);
        
        const composer = new EffectComposer( renderer );
        composer.addPass( renderScene );
        composer.addPass( bloomPass );
        composer.addPass( outputPass );
        // composer.addPass( customOutline );
        composer.addPass(effectFXAA);

        // const loader = new GLTFLoader();
        // loader.load('models/lbw_house.glb', (gltf) => {
        //     sceneEl.object3D.add(gltf.scene);

        //     //  Mesh  Surface ID 
        //     addSurfaceIdAttributeToMesh(gltf.scene);
        // });

        // function addSurfaceIdAttributeToMesh(scene) {
        //     const surfaceFinder = new FindSurfaces();
        //     surfaceFinder.surfaceId = 0;

        //     scene.traverse((node) => {
        //         if (node.isMesh) {
        //             const colorsTypedArray = surfaceFinder.getSurfaceIdAttribute(node);
        //             node.geometry.setAttribute("color", new THREE.BufferAttribute(colorsTypedArray, 4));
        //         }
        //     });

        //     customOutline.updateMaxSurfaceId(surfaceFinder.surfaceId + 1);
        // }
  

        let isDigest = false;
        renderer.render = function () {
            if (isDigest) {
                render.apply(renderer, arguments);
            } else {
                isDigest = true;
                composer.render();
                isDigest = false;
            }
        }

        window.addEventListener( 'resize', onWindowResize );
        function onWindowResize() {
            let _width = window.innerWidth;
			let _height = window.innerHeight;
            sceneEl.renderer.setSize( parseInt(_width), parseInt(_height) );
            sceneEl.camera.aspect = parseInt(_width)/parseInt(_height);
            sceneEl.camera.updateProjectionMatrix();
            // customOutline.setSize(_width, _height);
		}
        onWindowResize();
    }
});